<template>
  <v-row class="category">
    <v-col cols="9">
      <v-chip prepend-icon="mdi-list-box-outline" link @click="fnSeeAll"
        >Ï†ÑÏ≤¥Î≥¥Í∏∞</v-chip
      >
    </v-col>

    <v-col cols="3">
      <v-text-field
        variant="underlined"
        v-model="searchKeyword"
        append-icon="mdi-magnify"
        placeholder="Í≤ÄÏÉâÏñ¥ ÏûÖÎ†•"
        @click:append="fnSearchPosts"
        @keyup="fnSearchPosts"
      ></v-text-field>
    </v-col>
  </v-row>
  <v-row v-for="(i, idx) in rows" :key="i" class="loggingRow">
    <v-col
      v-for="post in displayPosts.filter((e, index) => {
        if (index >= idx * 3 && index < (idx + 1) * 3) return e;
      })"
      :key="post"
      cols="4"
    >
      <router-link :to="`/log/` + post.seq" class="linkPost">
        <v-card class="post" link>
          <v-card-title class="postTitle">
            {{ post.title }}
          </v-card-title>

          <v-divider></v-divider>

          <div class="dateDiff">
            {{ post.dateDiff }}
          </div>
        </v-card>
      </router-link>
    </v-col>
  </v-row>
</template>

<script>
import posts from "/public/json/_posts.json";

export default {
  name: "logList",
  data() {
    return {
      displayPosts: null,
      rows: 0,
      searchKeyword: "", // data searchKeyword Ï∂îÍ∞Ä
    };
  },
  created() {
    // vue ÏÉùÏÑ± Ïãú
    this.displayPosts = this.getAllPosts(posts);
  },
  methods: {
    // Í≤åÏãúÍ∏Ä Ï†ÑÏ≤¥ Í∞ÄÏ†∏Ïò§Í∏∞
    getAllPosts(posts) {
      // json ÌååÏùº javascript Î∞∞Ïó¥ÌòïÌÉúÎ°ú ÌëúÏãú
      let tmpPostsList = JSON.parse(JSON.stringify(posts));
      // ÏûëÏÑ±ÏùºÎ°úÎ∂ÄÌÑ∞Ïùò ÎÇ†Ïßú Ï∞®Ïù¥ ÏÑ§Ï†ï
      this.dateDiff(tmpPostsList);

      // seq Ïó≠ÏàúÏúºÎ°ú Ï†ïÎ†¨ÌïòÏó¨ return
      return tmpPostsList.sort(function (a, b) {
        return b.seq - a.seq;
      });
    },
    // dateDiff ÏÑ§Ï†ï
    dateDiff(posts) {
      let rst = "";

      for (var i in posts) {
        var dateParam = posts[i].date.substr(0, 10).split("-"); // date Í∞í ÎÖÑ,Ïõî,ÏùºÎ°ú Î∂ÑÌï†Ìïú Î∞∞Ïó¥
        var timeParam = posts[i].date.substr(10).trim().split(":"); // date Í∞í Ïãú,Î∂Ñ,Ï¥àÎ°ú Î∂ÑÌï†Ìïú Î∞∞Ïó¥

        // millisecondsÎ°ú Ïò§ÎäòÍ≥º ÏûëÏÑ±ÏùºÏùò Ï∞®Ïù¥Î•º Íµ¨Ìï®
        let diff =
          new Date().getTime() -
          new Date(
            dateParam[0],
            dateParam[1] - 1,
            dateParam[2],
            timeParam[0],
            timeParam[1],
            timeParam[2]
          ).getTime();

        // millisecond / 1000 => Ï¥àÎã®ÏúÑÎ°ú Î≥ÄÍ≤Ω
        var t = Math.ceil(diff / 1000);

        if (t < 60) {
          // tÍ∞Ä 60Ï¥à Î≥¥Îã§ ÏûëÏúºÎ©¥ nÏ¥à Ï†Ñ
          rst = t + "Ï¥à Ï†Ñ";
        } else if (t >= 60 && t < 60 * 60) {
          // tÍ∞Ä 60Ï¥àÎ≥¥Îã§ ÌÅ¨Í±∞ÎÇò Í∞ôÍ≥† 60Î∂ÑÎ≥¥Îã§ ÏûëÏúºÎ©¥ nÎ∂Ñ Ï†Ñ
          rst = Math.ceil(t / 60) + "Î∂Ñ Ï†Ñ";
        } else if (t >= 60 * 60 && t < 60 * 60 * 24) {
          // tÍ∞Ä 60Î∂ÑÎ≥¥Îã§ ÌÅ¨Í±∞ÎÇò Í∞ôÍ≥† 24ÏãúÍ∞ÑÎ≥¥Îã§ ÏûëÏúºÎ©¥ nÏãúÍ∞Ñ Ï†Ñ
          rst = Math.ceil(t / (60 * 60)) + "ÏãúÍ∞Ñ Ï†Ñ";
        } else if (t >= 60 * 60 * 24 && t < 60 * 60 * 24 * 30) {
          // tÍ∞Ä 24ÏãúÍ∞ÑÎ≥¥Îã§ ÌÅ¨Í±∞ÎÇò Í∞ôÍ≥† 30ÏùºÎ≥¥Îã§ ÏûëÏúºÎ©¥ nÏùº Ï†Ñ
          rst = Math.ceil(t / (60 * 60 * 24)) + "Ïùº Ï†Ñ";
        } else if (t >= 60 * 60 * 24 * 30 && t < 60 * 60 * 24 * 365) {
          // tÍ∞Ä 30ÏùºÎ≥¥Îã§ ÌÅ¨Í±∞ÎÇò Í∞ôÍ≥† 365ÏùºÎ≥¥Îã§ ÏûëÏúºÎ©¥ nÍ∞úÏõî Ï†Ñ
          rst = Math.ceil(t / (60 * 60 * 24 * 30)) + "Í∞úÏõî Ï†Ñ";
        } else if (t >= 60 * 60 * 24 * 365) {
          // tÍ∞Ä 365ÏùºÎ≥¥Îã§ ÌÅ¨Í±∞ÎÇò Í∞ôÏúºÎ©¥ nÎÖÑ Ï†Ñ
          rst = Math.ceil(t / (60 * 60 * 24 * 365)) + "ÎÖÑ Ï†Ñ";
        }

        // dateDiffÏóê Í∞í ÏÑ§Ï†ï
        posts[i].dateDiff = rst;
      }
    },
    // ÎûúÎç§ Î∞∞Í≤ΩÏÉâ ÏÑ§Ï†ï
    setPostBg() {
      let postTitle = document.querySelectorAll(".postTitle");

      if (postTitle.length > 0) {
        for (var i = 0; i < postTitle.length; i++) {
          var rColor = Math.floor(Math.random() * 256);
          var gColor = Math.floor(Math.random() * 256);
          var bColor = Math.floor(Math.random() * 256);

          postTitle[i].style.backgroundColor =
            "rgb(" + rColor + "," + gColor + "," + bColor + ", 0.1)";
        }
      }
    },
    // Í≤ÄÏÉâ Í∏∞Îä• Î©îÏÑúÎìú Ï∂îÍ∞Ä
    fnSearchPosts(evt) {
      if (
        evt.keyCode == null || // 1. Í≤ÄÏÉâ Î≤ÑÌäº ÎßàÏö∞Ïä§ ÌÅ¥Î¶≠
        (evt.keyCode != null && evt.keyCode == "13") // 2. ÏóîÌÑ∞ÏûÖÎ†•
      ) {
        // ÎèôÏ†ÅÏúºÎ°ú Î≥ÄÍ≤ΩÎêòÎäî data Í∞í Î≥ÄÏàò ÏÑ†Ïñ∏
        var key = this.searchKeyword.trim();

        // Í≤ÄÏÉâÏñ¥ null Ï≤¥ÌÅ¨
        if (key == "") {
          alert("Í≤ÄÏÉâÏñ¥Î•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.");
          return false;
        }

        // displayPosts Î≥ÄÍ≤Ω
        // Ï†ÑÏ≤¥ Ìè¨Ïä§Ìä∏ get -> titleÏóê keyÍ∞Ä Ìè¨Ìï®Îêú Í≤åÏãúÎ¨ºÎßå Ï°∞Ìöå
        this.displayPosts = this.getAllPosts(posts).filter(
          (post) => post.title.toUpperCase().indexOf(key.toUpperCase()) > -1
        );
      }
    },
    // Ï†ÑÏ≤¥ Í≤åÏãúÎ¨º Î™©Î°ù Ï∂úÎ†•
    fnSeeAll() {
      this.displayPosts = this.getAllPosts(posts);
    },
  },
  watch: {
    displayPosts(v) {
      setTimeout(this.setPostBg, 10);
      this.rows = Math.ceil(v.length / 3);
    },
  },
};
</script>

<style lang="scss">
.category {
  margin: 0.3rem;
}

.post:hover {
  top: -0.3em;
  box-shadow: 0.3em 0.3em 1em lightgray;
}

.linkPost {
  text-decoration: none;
}

.v-card-title {
  display: flex !important;
  align-items: center;
  justify-content: center;
  text-align: center;
  white-space: pre-wrap !important;
  height: 10em;
}

.dateDiff {
  padding: 10px 20px 10px 20px;
  text-align: right;
  font-style: italic;
  color: darkgrey;
}
</style>
